/*------------------------------------*\
    #Call To Action
\*------------------------------------*/

.cta h3 {
    font-size: 24px;
    font-weight: 500;
    font-style: italic;
    line-height: 51px;
    margin-bottom: 0;
    color: @color-white;
}

.cta p {
    color: #f9f9f9;
    font-size: 17px;
    font-weight: 400;
    line-height: 26px;
    margin-bottom: 25px;
}

.cta-1 {
    padding: 20px 0;
    background-image: -moz-linear-gradient( 180deg, rgb(248, 161, 55) 0%, rgb(251, 172, 71) 64%, rgb(254, 183, 86) 100%);
    background-image: -webkit-linear-gradient( 180deg, rgb(248, 161, 55) 0%, rgb(251, 172, 71) 64%, rgb(254, 183, 86) 100%);
    background-image: -ms-linear-gradient( 180deg, rgb(248, 161, 55) 0%, rgb(251, 172, 71) 64%, rgb(254, 183, 86) 100%);
    background-image: linear-gradient( 180deg, rgb(248, 161, 55) 0%, rgb(251, 172, 71) 64%, rgb(254, 183, 86) 100%);
}

/* Call To Action #2 */

.cta-2 .cta--content {
    padding-right: 50px;
}

.cta-2 .cta--content h3 {
    font-size: 36px;
    line-height: 45px;
    margin-bottom: 32px;
}

.cta-2 .cta--content p:last-of-type {
    margin-bottom: 45px;
}

.cta-2 .cta--content .download--btns a {
    background: linear-gradient(to left, #f8a137 0%, #feb756 100%);
    color: @color-white;
    height: 70px;
    line-height: 70px;
    text-transform: capitalize;
    font-style: italic;
    font-size: 15px;
    transition: all .4s linear;
    border-radius: 2px;
}

.cta-2 .cta--content .download--btns a:hover {
    background: linear-gradient(to left, #feb756 0%, #f8a137 100%);
}

.cta-2 .cta--content .download--btns a img {
    margin-right: 12px;
}

/* Call To Action #3 */

.cta-3 {
    padding: 0;
    position: relative;
    z-index: 2;
}

.cta-3 .cta-container {
    background-color: #232323;
    padding-top: 30px;
    padding-bottom: 37px;
}

.cta-3 h3 {
    color: @color-white;
}

/* Small Devices, Tablets */

@media only screen and (min-width: 320px) and (max-width: 767px) {
    .cta h3 {
        text-align: center;
        margin-bottom: 30px;
    }

    .cta .text-right {
        text-align: center !important;
    }
    .cta-2 .cta--content{
        padding-right: 0;
    }
    .cta-2 .download--btns a {
        margin:15px auto !important;
        display: block;
    }
    .cta-2 .cta--content p{
        text-align: center;
    }
    

}

/* Small Devices, Tablets */

@media only screen and (min-width: 768px) and (max-width: 991px) {
    .cta h3 {
        text-align: center;
        margin-bottom: 30px;
    }

    .cta .text-right {
        text-align: center !important;
    }
    .cta-2 .download--btns{
        text-align: center;
    }
    .cta-2 .cta--content p{
        text-align: center;
    }
    .cta-2  .mockup--img img{
        margin-right: auto;
        margin-left: auto;
    }

}


@media only screen and (min-width: 992px) and (max-width: 1200px) {
    .download--btns{
        margin-bottom: 20px !important;
    }

    .cta-2 .cta--content h3 {
        font-size: 26px;
        line-height: 35px;
        margin-bottom: 20px;
    }

    .cta-2 p{
        font-size:14px; 
    }
.cta-2 .cta--content p:last-of-type {
    margin-bottom: 15px;
}

}